import React, { FC, useCallback, useEffect, useState } from "react";
import { SignupModalWrapper } from "./styles";
import { Form } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "slices";
import { toastErrorMessage, toastSuccessMessage } from "config";
import Checkbox from "antd/lib/checkbox/Checkbox";
import router from "next/router";
import useInput from "@hooks/useInput";
import axios from "axios";
interface IProps {
  onFinishSignUp: (values: any) => void;
  onClickSignUpToggle: (e: any) => void;
  setOnSignUp: (value: boolean) => void;
}

const formItemLayout = {
  labelCol: { span: 24 },
  wrapperCol: { span: 24 },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
  },
};

const SignupModal: FC<IProps> = ({ onFinishSignUp, onClickSignUpToggle, setOnSignUp }) => {
  const [form] = Form.useForm();
  const { signupDone } = useSelector((state: RootState) => state.user);
  const [email, onChangeEmail] = useInput("");
  const [onEmailCheckForm, setOnEmailCheckForm] = useState(false);

  useEffect(() => {
    if (signupDone) {
      toastSuccessMessage(` ! .`);
      setOnSignUp(false);
    }
  }, [signupDone]);

  const onClickSendEmailAuth = useCallback(
    (e) => {
      e.preventDefault();
      if (!email || !email?.trim()) {
        toastErrorMessage(" .");
        return;
      }
      if (!email.match(/[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-Za-z0-9\-]+/)) {
        toastErrorMessage("   .");
        return;
      }
      axios
        .post("/auth/email", { email })
        .then(() => {
          setOnEmailCheckForm(true);
          toastSuccessMessage(`${email}  .  .`);
        })
        .catch((error) => {
          toastErrorMessage(error);
          throw error;
        });
    },
    [email]
  );

  return (
    <SignupModalWrapper>
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinishSignUp}
        initialValues={{
          prefix: "86",
        }}
        scrollToFirstError
      >
        <div className="email-wrapper">
          <Form.Item
            name="email"
            label=""
            rules={[
              {
                type: "email",
                message: "  .",
              },
              {
                required: true,
                message: " .",
              },
            ]}
          >
            <input onChange={onChangeEmail} value={email} type="email" />
          </Form.Item>
          <Form.Item label=" ">
            <button onClick={onClickSendEmailAuth}>
              <span> </span>
            </button>
          </Form.Item>
        </div>
        {onEmailCheckForm && (
          <Form.Item
            className="email-check-wrapper"
            name="authNum"
            label=""
            rules={[
              {
                required: true,
                message: " 6 .",
              },
            ]}
          >
            <input type="number" />
          </Form.Item>
        )}
        <div className="form-name">
          <Form.Item
            name="first_name"
            label=""
            rules={[{ required: true, message: " .", whitespace: true }]}
          >
            <input type="text" />
          </Form.Item>
          <Form.Item
            name="last_name"
            label=""
            rules={[{ required: true, message: " .", whitespace: true }]}
          >
            <input type="text" />
          </Form.Item>
        </div>
        <Form.Item
          name="password"
          label=""
          rules={[
            {
              required: true,
              message: " .",
            },
            {
              min: 9,
              message: " 9  .",
            },
          ]}
          hasFeedback
        >
          <input type="password" />
        </Form.Item>
        <Form.Item
          name="agreement"
          valuePropName="checked"
          style={{ marginTop: "1rem" }}
          rules={[
            {
              validator: (_, value) =>
                value ? Promise.resolve() : Promise.reject(new Error(" .")),
            },
          ]}
          {...tailFormItemLayout}
        >
          <Checkbox>
             ,   .{" "}
            <a onClick={() => router.push("/about#policy")} className="term">
              
            </a>
          </Checkbox>
        </Form.Item>
        <div className="btn-wrapper">
          <button className="btn-point" onSubmit={onFinishSignUp}>
            
          </button>
          <button onClick={onClickSignUpToggle}></button>
        </div>
      </Form>
    </SignupModalWrapper>
  );
};

export default SignupModal;
